body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 20px;
  color: #666;
  font-weight: normal;
}

* {
  /* border: 1px solid black; */
}

.container {
  width: 1000px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.content-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.title {
  text-align: center;
  margin: 25px 0 60px 0;
}

.title h1 {
  text-align: center;
  font-size: 35px;
}

.map-container {
  width: 450px;
}

.get-started-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 450px;
}

.get-started-container p {
  margin: 0;
}

.get-started-container h1 {
  font-size: 75px;
  margin: 0;
}

.no-data-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 450px;
}

.no-data-container p {
  margin: 0;
}

.no-data-container h1 {
  font-size: 35px;
  margin: 15px 0 15px 0;
}

.chart-container {
  width: 450px;
}

.loading-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 450px;
}

.loading-container p {
  margin-bottom: 25px;
}
